





















































































import { Component, Vue } from "vue-property-decorator";
import DataService from "../services/DataService";

@Component
export default class Match extends Vue {
  year = 0;
  selectableYears: number[] = [];
  match: any = {
    oldGoals: null,
    youngGoals: null,
  };
  tableaux: any = [
    {
      rank: null,
      name: null,
      scoredGoals: 0,
      goalDifference: 0,
      points: 0,
    },
  ];
  lastMatches: any = [
    {
      homeTeam: null,
      awayTeam: null,
      homeGoals: null,
      awayGoals: null,
      date: null,
    }
  ];

  async saveMatch() {
    if(confirm("Ergebnis " + this.match.oldGoals + ":" + this.match.youngGoals + " eintragen?")) {
      let data = {
        oldGoals: Number.parseInt(this.match.oldGoals),
        youngGoals: Number.parseInt(this.match.youngGoals),
      };
      await DataService.saveMatch(data);
      this.updateTableaux();
      this.updateLastMatches();
    }
  }

  async updateTableaux() {
    try {
      this.tableaux = (await DataService.getTableaux(false, this.year, -1)).data;
    } catch (error) {
      console.error(error);
    }
  }

  async updateLastMatches() {
    try {
      this.lastMatches = (await DataService.getMatches(false, 5)).data;
    } catch (error) {
      console.error(error);
    }
  }

  setCurrentYear(current: number) {
    this.year = current;
    this.updateTableaux();
  }

  mounted() {
    for (let year = new Date().getFullYear()-1; year >= 2017; year--) {
      this.selectableYears.push(year);
    }
    this.updateTableaux();
    this.updateLastMatches();
  }
}
